.home{
    flex-grow: 1;
    margin-top: 10vh;
    min-width:100%;
    display:flex;
    flex-direction: column;
    align-items: center;
}

.home .title{
    width: 100%;
    font-size: 18px;
    text-align: center;
}

.home .title .p{
    line-height: 50px;
}

.home .person{
    width: 100%;
    margin-top: 20px;
}

.home .person img{
    width: 100%;
}

@media (min-width: 800px){
    .home{
        flex-direction: row;
        justify-content: space-between;
    }
    .home .title,
    .home .person{
        width: 50%;
        margin-top: 0;
        text-align: left;
        font-size: 25px;
    }
    .home .person img{
        width: 100%;
    }
}